<template>
  <div id="app">
    <button class="reset" @click="reset">Reset</button>
    <div class="grid">
      <div class="item"
           v-for="code in order"
           :key="code"
           @click="toggle(code)">
        <!--        :class="{ selected: isSelected(code), answer: isAnswer(code) }"-->
        <img class="symbol"
             :src="img(code)"
             :class="{ selected: isSelected(code), answer: isAnswer(code) }">
      </div>
    </div>
  </div>
</template>

<script>
import img000 from './assets/000.png';
import img001 from './assets/001.png';
import img010 from './assets/010.png';
import img011 from './assets/011.png';
import img100 from './assets/100.png';
import img101 from './assets/101.png';
import img110 from './assets/110.png';
import img111 from './assets/111.png';

// function isCombinedValid(combined) {
//   if (combined.length !== 3) {
//     return false;
//   }
//
//   let countOf1And3 = 0;
//   countOf1And3 += (combined[0] === '1' || combined[0] === '3');
//   countOf1And3 += (combined[1] === '1' || combined[1] === '3');
//   countOf1And3 += (combined[2] === '1' || combined[2] === '3');
//
//   return countOf1And3 === 1;
// }
//
// function findCode(selection, idx, val) {
//   console.log(selection, idx, val);
// }

export default {
  name: 'App',
  data() {
    return {
      order: ['000', '001', '010', '011', '100', '101', '110', '111'],
      selection: []
    }
  },
  computed: {
    combined() {
      if (this.selection.length === 4) {
        const reducer = (accumulator, currentValue) => Number(accumulator) + Number(currentValue);
        return String(this.selection.reduce(reducer));
      } else {
        return '';
      }
    },
    hasAnswer() {
      if (this.combined.length !== 3) {
        return false;
      }

      let hint = 0;
      hint += (this.combined[0] === '1' || this.combined[0] === '3') ? 4 : 0;
      hint += (this.combined[1] === '1' || this.combined[1] === '3') ? 2 : 0;
      hint += (this.combined[2] === '1' || this.combined[2] === '3') ? 1 : 0;

      return (hint === 4) || (hint === 2) || (hint === 1);
    },
    answer() {
      if (!this.hasAnswer) {
        return '';
      }

      const indexOf1 = this.combined.indexOf('1');
      const indexOf3 = this.combined.indexOf('3');

      let idx, target;
      if (indexOf1 > indexOf3) {
        idx = indexOf1;
        target = '1';
      } else {
        idx = indexOf3;
        target = '0';
      }

      return this.selection.find(code => code[idx] === target);
    }
  },
  methods: {
    img(code) {
      switch (code) {
        case '000':
          return img000;
        case '001':
          return img001;
        case '010':
          return img010;
        case '011':
          return img011;
        case '100':
          return img100;
        case '101':
          return img101;
        case '110':
          return img110;
        case '111':
          return img111;
      }
    },
    toggle(code) {
      const index = this.selection.indexOf(code);
      if (index > -1) {
        this.selection.splice(index, 1);
      } else {
        this.selection.push(code);
      }
    },
    isSelected(code) {
      return this.selection.indexOf(code) > -1;
    },
    isAnswer(code) {
      return this.answer === code;
    },
    reset() {
      this.selection = [];
    }
  }
}
</script>

