  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(4, 1fr);
}

.symbol {
  box-sizing: border-box;
  width: 90%;
  /*opacity: 70%;*/
}

.item {
  text-align: center;
  padding: 1ch;
}

.selected {
  border-radius: 3ch;
  /*border: solid 1px red;*/
  border: dashed 1px red;
  background-color: darkgray;
  /*background-color: slategray;*/
}

.answer {
  border: dashed 2px gold;
  /*background-color: darkgray;*/
  background-color: slategray;
}

</style>
